@media screen and (min-width:640px) {
    .article{
        height:170px;
        position:relative;
        padding-left:170px;
        margin-bottom:20px;
        font-family: 'Fox Bebas';
    }
    .article .article-img{
        position: absolute;
        top:0px;
        left:0px;
        width:170px;
        height:170px;
        background:#0d0f01;
        display:flex;
        justify-content: center;
    }
    .article .article-img img{
        width:100%;
    }
    .article a{
        text-decoration: none;
    }
    .article h2{
        color:#000;
        text-indent:20px;
        line-height:50px;
        height:50px;
        margin:0px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .article h2:hover{
        text-decoration: underline;
    }
    .article label{
        color:slategray;
        padding-left:20px;
    }
    .article p{
        padding-left:20px;
    }
    .article .tags{
        padding-left:20px;
        padding-top:5px;
        white-space: nowrap;
    }
    .article-tag{
        padding:15px;
        padding-top:0px;
        padding-bottom: 0px;
        height:40px;
        display: inline-block;
        background:#d3ff02;
        border-right:1px solid #000;
        cursor:pointer;
        line-height:45px;
    }
    .article-tag:hover{
        background:#b7ff0e;
    }
    .article .tags > .article-tag:nth-last-of-type(1){
        border-right:0px;
    }
}
@media screen and (max-width:640px){
    .article{
        width:80%;
        margin:0 auto;
        text-align: center;
        position: relative;
        margin-bottom:60px;
    }
    .article-img{
        width:100%;
        height:240px;
        background:#cccccc;
        overflow: hidden;
    }
    .article-img img{
        width:100%;
        height:100%;
    }
    .article .article-content{
        background:#e7e7e7;
    }
    .article .article-content h2{
        margin:0px;
        color:#000;
        height:60px; 
        line-height:60px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width:75%;
        margin:0 auto;
    }
    .article .article-content a{
        text-decoration: none;
        text-decoration-color: #000;
    }
    .article .article-content a:hover{
        text-decoration: underline;
    }
    .article .article-content label{
        font-family: 'Fox Bebas';
    }
    .article .article-content p{
        font-family: 'Fox Bebas';
        letter-spacing: 0.7px;
        width:75%;
        margin:0 auto;
        margin-top:30px;
        margin-bottom:30px;
    }
    .article .article-content .tags{
        padding-bottom:40px;
    }
    .article-content .tags .article-tag{
        padding:15px;
        padding-top:0px;
        padding-bottom: 0px;
        height:40px;
        display: inline-block;
        background:#d3ff02;
        border-right:1px solid #000;
        cursor:pointer;
        line-height:45px;
        font-family: 'Fox Bebas';
    }
    .article .tags > .article-tag:nth-last-of-type(1){
        border-right:0px;
    }
    .article-tag:hover{
        background:#b7ff0e !important;
    }
}