.os-window{
    width:100%;
    height:600px;
    background:#151515;
    border-radius:9px;
    text-align: left;
    box-shadow:0px 0px 6px 3px #00000025;
    margin-bottom:50px;
    transform-origin: 0px 0px;
    transform-style: preserve-3d;
}
@media screen and (max-width:640px){
    .os-window{
        transform:scale(0.5) translateX(0.25);
    }
}
@media screen and (max-width:960px){
    .os-window{
        transform:scale(0.75);
    }
}
@media screen and (min-width:960px){
    .os-window{
        transform:scale(1);
    }
}
.os-window .titlebar{
    position:absolute;
    top:0px;left:0px;right:0px;height:40px;
    background:#121212;
    border-radius:9px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    line-height:40px;
    color:#fff;
    text-indent:15px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size:12px;
    font-weight: lighter;
}
.os-window .window-content{
    position: absolute;
    top:50px;left:10px;
    right:10px;
    bottom:10px;
}
.os-window .window-content [feature]{
    transform: translateZ(0px);
    transition: transform 0.2s linear;
    transform-style: preserve-3d;
}
.os-window .window-content [feature]:hover {
    transform:translateZ(50px) !important;
}
.stream-preview{
    width:50%;
    height:100%;
    background:#181818;
}
.stream-preview img{
    width:100%;
}
.stream-preview .stream-info{
    position:absolute;bottom:0px;left:0px;right:0px;height:210px;
}
.stream-info h2{
    margin:0px;
    height:50px;
    line-height: 60px;
    text-indent: 20px;
    border-bottom:1px solid #00000025;
    transform:translateZ(20px);
}
.stream-info .service{
    width:80px;
    height:80px;
    color:#fff;
    text-align: center;
    line-height: 80px;
    border:1px solid #00000025;
    font-size:24px;
    background:#ffffff05;
    display:inline-block;
}
.stream-info .service.active{
    color:#d3ff02;
    text-shadow: 0px 0px 4px #d3ff02;
    transform-style: preserve-3d;
}
.stream-info .service.active i{
    transform:translateZ(20px);
}
.os-window .plugins-and-stats{
    position:absolute;
    top:0px;right:0px;width:calc(50% - 10px);bottom:0px;
}
.plugins-and-stats .column{
    border:1px solid #ffffff25;
    width:150px;
    height:100%;
}
.plugins-and-stats .column h2{
    margin:0px;
    height:40px;
    line-height:45px;
    text-align: center;
    border-bottom:1px solid #ffffff25;
}
.plugins-and-stats .actions{
    position:absolute;top:40px;left:0px;width:150px;
    bottom:0px;
    overflow-y:auto;
    list-style: none;
}
.plugins-and-stats .actions li{
    height:40px;
    line-height: 40px;
    text-align: center;
    color:#fff;
    font-size:13px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border-bottom:1px solid #ffffff25;
    cursor:pointer;
}
.plugins-and-stats .actions li:hover{
    background:#ffffff05;
}
.plugins-and-stats .sources{
    position:absolute;top:0px;right:0px;left:150px;bottom:0px;overflow-y:auto;
}
.sources > h2{
    text-indent:30px;
}
.sources .source{
    margin:5px;
    height:100px;
    border:1px solid #ffffff10;
    overflow:hidden;
    padding-left:155px;
    position: relative;
}
.sources .source img{
    position: absolute;
    top:10px;
    left:10px;
    width:135px;
    height:80px;
}
.sources .source h2{
    margin:0px;
    height:40px;
    line-height: 50px;
    font-size:14px;
}
.sources .source label{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color:#fff;
    font-weight:lighter;
    font-size:12px;
}