.radar{
    position: relative;
    overflow:hidden;
}
.radar img{
    width:100%;
    height:100%;
}
.scanner-overlay{
    position:absolute;
    top:0px;
    left:0px;
    right:0px;
    bottom:0px;
}
.scanner-beam{
    position:absolute;top:0px;bottom:0px;width:1px;
    background:#d3ff0285;
    box-shadow: -10px 0px 60px 25px #d3ff0250;
}
.radar .blip{
    position:absolute;
    width:10px;
    height:10px;
    border-radius: 100%;
    background:#d3ff02;
    box-shadow:0px 0px 4px 2px #d3ff02;
    transition:opacity 0.2s linear;
}