.navigation{
    position: fixed;top:0px;
    left:0px;
    width:0px;
    bottom:0px;
    background:#222;
    overflow:hidden;
    transition: width 0.2s linear;
    z-index: 999;;
}
.navigation.open{
    width:80vw;
    overflow-y:auto;
}
@media screen and (min-width:640px) {
    .navigation.open{
        display: none;
    }
}
.navigation > button:nth-child(1){
    position:absolute;top:0px;right:0px;width:50px;height:51px;
    color:#fff;
    background:transparent;
    border:0px;
    font-size:20px;
    border-bottom:1px solid #000;
    line-height: 48px;
    cursor:pointer;
    transition: color 0.2s linear;
    border-left:1px solid #00000020;
    background:#00000035;
}
.navigation > button:nth-child(1):hover{
    color:#d3ff02;
}
.navigation .nav-header {
    position: absolute;
    top:0px;
    left:0px;
    right:50px;
    height:50px;
    border-bottom:1px solid #000;
    line-height:55px;
    text-indent:20px;
    color:#d3ff02;
    font-family: 'Fox Bebas';
    background:#00000035;
}
.navigation .nav-links{
    position:absolute;top:51px;left:0px;right:0px;bottom:0px;
    overflow-y:auto;
}
.navigation .nav-links a{
    display: block;
    height:60px;
    line-height: 60px;
    text-indent:25px;
    border-bottom:1px solid #00000030;
    color:#fff;
    text-decoration: none;
    font-family: 'Fox Bebas';
    letter-spacing: 1px;;
    background: transparent;
    transition:all 0.2s linear;
}
.navigation .nav-links a:after{
    content: "\f105";
    font-family: 'Font Awesome 6 Free';
    float:right;
    position: relative;
    left:-20px;
    transition:all 0.2s linear;
    text-shadow: 0px 0px 4px transparent;
}
.navigation .nav-links a:hover{
    color:#d3ff02;
    text-shadow: 0px 0px 4px #d3ff02;
    background:rgba(0 , 0 , 50 , 0.05);
}