footer{
    background:#080808;
    height:400px;
    position: relative;
}

@media screen and (min-width:1200px){
    .leftblock{
        position:absolute;top:-40px;
        left:-100px;
        width:550px;
        height:75px;
        background:#02ff74;
        transform:skew(-20deg);
        box-shadow: 0px 0px 12px 4px #ffffff30;
    }
    .leftblock img{
        transform: skew(20deg);
        position: relative;
        left:210px;
        top:-35px;
        width:150px;
    }
    .footer-content .contact{
        position:absolute;top:50px;
        left:10px;
        width:420px;
        bottom:10px;
        padding-top:55px;
        padding-left:30px;
    }
    .contact .rounded-border{
        width:40px;
        height:40px;
        display: inline-block;
        border:2px solid #fff;
        border-radius:100%;
        color:#fff;
        text-align: center;
        margin:20px;
        margin-top:10px;
        margin-bottom:10px;
    }
    .rounded-border i{
        line-height: 40px;
    }
    .contact , .contact a{
        color:#fff;
        font-family: 'Fox Bebas';
        text-decoration: none;
        letter-spacing: 1px;
    }
    .footer-content .links{
        position:absolute;top:50px;left:500px;width:800px;display:block;
    }
    .footer-content .links .link{
        display:inline-grid;
        padding-left:30px;
        padding-right:30px;
    }
    .links .link ul{
        padding:0px;
    }
    .footer-content .link a:before{
        content: "\f105";
        margin-right:10px;
        font-family: 'Font Awesome 6 Free';
    }
    .footer-content .link a{
        display: block;
        height:30px;
        line-height: 30px;
        text-decoration: none;
        color:#fff;
        font-family: 'Fox Bebas';
        letter-spacing: 1px;
    }
    .footer-content .link a:hover{
        color:#d3ff02;
        text-shadow: 0px 0px 8px #d3ff02;
    }
}
