.logo-large{
    margin:0 auto;
    width:450px;
    position:relative;top:calc(50vh - 100px);
    color:#fff;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.logo-large img{
    width:450px;
}
h2{
    font-family: 'Fox Bebas';
    color:#d3ff02;
}
.logo-large {
    text-align:center;
}
.logo-large h2{
    font-size:40px;
}
.nuketown-radar{
    width:50%;
    height:400px;
    margin:0 auto;
    border-radius:8px;
    border:2px solid #cccccc25;
    box-shadow:0px 0px 4px 2px #d3ff0250;
    margin-bottom:70px;
}
@media screen and (max-width:640px) {
    .nuketown-radar{
        height:200px;
    }
}
.radar-feature {
    text-align: center;
    position: relative;
    box-shadow: inset 0px 0px 100px 2px #d3ff0225;
}
.streaming-feature {
    text-align: center;
    position: relative;
    padding-bottom:80px;
}
.radar-feature .background{
    background-image: url('../../media/grid.png');opacity:0.05;
    background-size:10% 10%;
    position:absolute;
    top:0px;
    left:0px;
}
.radar-feature p , .streaming-feature p {
    text-align: center;
    color:#fff;
    font-family: 'Fox Bebas';
    letter-spacing: 0.7px;;
}
.radar-feature .cta , .streaming-feature .cta{
    width:300px;
    height:45px;
    margin:0 auto;
    margin-top:50px;
    background:#33b63e;
    color:#000;
    border:0px;
    font-family: 'Fox Bebas';
    font-size:18px;
    line-height: 45px;
    box-shadow: 2px 2px 4px 2px #00000050;
    cursor:pointer;
}
.radar-feature .cta:hover , .streaming-feature .cta:hover{
    background:#d3ff02;
}